.sidebar {
  z-index: 1000;
  background-color: #f0f0f0;
  width: 300px;
  height: 100%;
  padding: 20px;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  box-shadow: 2px 0 5px #0003;
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar h2 {
  margin-top: 0;
}

.sidebar ul {
  padding: 0;
  list-style: none;
}

.sidebar ul li {
  margin-bottom: 10px;
}

.sidebar ul li a {
  color: #333;
  background-color: #ddd;
  border-radius: 5px;
  padding: 10px;
  text-decoration: none;
  display: block;
}

.sidebar ul li a:hover {
  background-color: #ccc;
}

.close-btn {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.sidebar ul li a.btn-custom {
  text-align: left;
  color: #333;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #f0f0f0;
  border: none;
  border-radius: 4px;
  height: 50px;
  margin-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  line-height: 50px;
  text-decoration: none;
  transition: background-color .2s ease-in-out;
  display: block;
}

.sidebar ul li a.btn-custom:hover {
  background-color: #e0e0e0;
}

.sidebar ul li a.btn-custom.selected {
  color: #fff;
  background-color: #202124;
}

@font-face {
  font-family: Material Icons;
  font-style: normal;
  font-weight: 400;
  src: local(Material Icons), local(MaterialIcons-Regular), url("MaterialIcons-Regular.748655a8.ttf") format("truetype");
}

.material-icons {
  text-transform: none;
  letter-spacing: normal;
  white-space: nowrap;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  direction: ltr;
  font-family: Material Icons;
  font-size: 24px;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  display: inline-block;
}
